const app = getApp();
export default {
	data() {
		return {
			info: {},
			imgUrl: '',
			dataType: '',
			usePoints: '',
			addressList: {},
		};
	},
	onLoad(option) {
		console.log(option)
		this.imgUrl = this.$helper.imgUrl;
		let item = JSON.parse(option.obj)
		for (let key in item) {
			for (let key2 in item[key]) {
				item['orderitem'] = item[key]
			}
		}
		this.info = item
		if (option.type == 'courseBuy') {
			this.dataType = 'curricula';
		} else if (option.type == 'books') {
			this.dataType = 'book';
		} else if (option.type == 'datas') {
			this.dataType = 'data';
		}
	},
	methods: {
		checkboxChange(e) {
			let isType = e.detail.value;
		},
		capyOrder(e){
			uni.setClipboardData({
				data: e,
				success: function (res) {
					uni.showToast({
						title: '复制成功',
						icon: 'none'
					})
				}
			});
		}
	}
};
